.gamecard {
    height: 16rem;
    width: 11rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: .15rem solid aliceblue;
    background-color: rgb(59, 59, 59, .9);
    box-shadow: rgba(9, 30, 66, 0.25) 0px 4px 8px -2px, rgba(9, 30, 66, 0.08) 0px 0px 0px 1px;
    border-radius: .5rem;
    gap: .5rem;
    cursor: pointer;
}

.gamecard:hover {
    background-color: rgb(59, 59, 59);
}

.gamecard img {
    max-width: 100%;
    max-height: 100%;
}

.gamecard h3 {
    width: 90%;
    text-align: center;
    background-color: aliceblue;
    border-radius: .2rem;
}

.gamecard:hover h3 {
    background-color: gold;
}

.gamecard-img-section {
    height: 80%;
}